import AUTH from '@/config/web/auth'
import UTIL from '@/common/util'

import jsonRpcService from "@/common/liveStreamJsonRpcService";

import JQ from "jquery";


export default {
    id: '',
    uid: '',
    vue: null,
    streamRoomId: '',
    init(params, vue) {
        this.id = params.id;
        this.uid = params.uid;
        this.streamRoomId = this.id + '_' + this.uid;
        this.vue = vue;
    },
    sendToUser(event, func) {
        let sendParams = {};
        switch (event.page) {
            case 'exam':
                if (event.action == 'sendProblem') {
                    this.sendRpcData(event.no, 'insertProblem', func);
                    sendParams.userReponseParams = {
                        parentId: this.id,
                        no: event.no,
                        type: 'insertProblem'
                    };
                    sendParams.userReponseShow = true;
                }
                sendParams.screenShow = 'close';
                sendParams.close = 'loadExamShow';
                break;
            case 'immExam':
                if (event.action == 'sendProblem') {
                    this.sendRpcData(event.no, 'insertProblem', func);
                    sendParams.userReponseParams = {
                        parentId: this.id,
                        no: event.no,
                        type: 'insertProblem'
                    };
                    sendParams.userReponseShow = true;
                }
                sendParams.screenShow = 'close';
                sendParams.close = 'loadImmExamShow';

                break;
            case 'quiz':
                if (event.action == 'sendQuiz') {
                    this.sendRpcData(event.no, 'insertQuiz', func);
                    sendParams.userReponseParams = {
                        parentId: this.id,
                        no: event.no,
                        type: 'insertQuiz'
                    };
                    sendParams.userReponseShow = true;
                }
                sendParams.screenShow = 'close';
                sendParams.close = 'loadQuizShow';

                break;
            case 'poll':
                if (event.action == 'sendPoll') {
                    this.sendRpcData(event.no, 'insertPoll', func);
                    sendParams.userReponseParams = {
                        parentId: this.id,
                        no: event.no,
                        type: 'insertPoll'
                    };
                    sendParams.userReponseShow = true;
                }
                sendParams.screenShow = 'close';
                sendParams.close = 'loadPollShow';
                break;
            case 'vote':
                if (event.action == 'sendVote') {
                    this.sendRpcData(event.no, 'insertVote', func);
                    sendParams.userReponseParams = {
                        parentId: this.id,
                        no: event.no,
                        type: 'insertVote'
                    };
                    sendParams.userReponseShow = true;
                }
                sendParams.screenShow = 'close';
                sendParams.close = 'loadPollShow';
                break;
            case 'userResponse':
                if (event.action == 'forceEnd') {
                    let msg = '';
                    switch (event.gtype) {
                        case 'insertProblem':
                            msg = '시험을';
                            break;
                        case 'insertPoll':
                            msg = '설문조사를';
                            break;
                        case 'insertVote':
                            msg = '투표를';
                            break;
                        case 'insertQuiz':
                            msg = '간단퀴즈를';
                            break;
                    }
                    let message = {
                        type: 'confirm',
                        message: msg + ' 강제 종료하시겠습니까?',
                        doAction: () => {
                            this.modalResponse('forceResponseEnd');
                            JQ('#app-response').hide();
                            func({cmd: 'responseClose'})

                        }
                    };
                    this.vue.$eventBus.$emit('modalOpen', message);

                } else if (event.action == 'barHide') {
                    switch (event.gtype) {
                        case 'insertProblem':
                            sendParams.responseTitle = '시험 진행중';

                            break;
                        case 'insertPoll':
                            sendParams.responseTitle = '설문 진행중';
                            break;
                        case 'insertVote':
                            sendParams.responseTitle = '투표 진행중';
                            break;
                        case 'insertQuiz':
                            sendParams.responseTitle = '간단퀴즈 진행중';
                            break;
                    }
                    JQ('#app-response').hide();
                    /*

                    if (this.timeResponseInterval) {
                        this.responseTimeInfo = '00:00:00'
                        clearInterval(this.timeResponseInterval);
                    }
                    let rtimeCnt = 0;

                    this.timeResponseInterval = setInterval(() => {
                        this.responseTimeInfo = this.setTimeVal(rtimeCnt);
                        rtimeCnt++;

                    }, 1000);
                    */
                    JQ('#menu-response-box').show();
                } else {
                    JQ('#app-response').hide();
                    sendParams.userReponseShow = 'close';
                }
                break;
            case 'fileView':
                sendParams.close = 'loadFileShow';
                sendParams.fileNo = 'empty';
                sendParams.screenShow = 'close';
                break;
            case 'data':
                if (event.action == 'share') {
                    sendParams.fileNo = event.fdata.id;
                    sendParams.open = 'loadFileShow';
                    let rpcParams = {
                        sendMethod: 'insertFdata',
                        data: event.fdata.id,
                        responseMethod: 'insertFdata',
                    }
                    jsonRpcService.send(rpcParams, (res) => {
                       // func({cmd: 'responseOpen', data: res});
                    });
                } else {
                    sendParams.screenShow = 'close';
                }
                sendParams.close = 'loadDataShow';

        }
        return sendParams;
    },
    sendRpcData(no, type, func) {
        let rpcParams = {
            sendMethod: type,
            data: no,
            responseMethod: type,
        }
        jsonRpcService.send(rpcParams, (res) => {
            func({cmd: 'responseOpen', data: res});
        });
        let msg;
        switch (type) {
            case 'insertProblem':
                msg = '시험을 출제하였습니다';
                break;
            case 'insertPoll':
                msg = '설문조사를 시작하였습니다';
                break;
            case 'insertVote':
                msg = '투료를 시작하였습니다';
                break;
            case 'insertQuiz':
                msg = '간단퀴즈를 출제하였습니다';
                break;
        }
        JQ('#app-response').show();
        let message = {
            type: 'timeout',
            message: msg,
        };
        this.vue.$eventBus.$emit('modalOpen', message);

    },
    rpcSend(params) {
        jsonRpcService.send(params, (res) => {

        });
    },
    modalResponse(type) {
        switch (type) {
            case 'forceResponseEnd':
                let rpcParams = {
                    sendMethod: 'forceResponseEnd',
                    data: 'none',
                    responseMethod: 'forceResponseEnd',
                }
                jsonRpcService.send(rpcParams, () => {
                });

                break;

        }
    },

}
