<template>
    <div class="zoom-layout">
        <div id="screen-init"></div>
        <!-------------------- mobile start -------------------->
        <div v-if="mobile">
            <div class="header fixed">
                <div class="subject-box">{{subject}}</div>
                <div class="layout-box menu-response-mobile" id="menu-response-box" @click="openResponse()">
                    <span>{{responseTitle}}</span> {{responseTimeInfo}}
                    <span class="circle-box">
                        <font-awesome-icon class="fa-square" icon="fa-solid fa-square"/>
                    </span>
                </div>

                <span class="time">
                    <font-awesome-icon icon="far fa-clock" class="fa-clock"></font-awesome-icon> {{timeInfo}}
                </span>
                <div class="end-button-mobile">
                    <v-btn class="end-button" type="button" @click="endMeeting()">끝내기</v-btn>
                </div>
            </div>
            <div class="menu" id="bottom-menu-box">
                <ul class="menu-box">
                    <li @click="loadMobileSubMenu('exam')">
                        <img src="@/assets/svg/menu/exam.svg" style="width:27px;"><span
                            class="title-over posi-top">시험</span> <span class="is-dot" v-if="IsProblem"></span>

                    </li>
                    <li @click="loadComponent('quiz')"><img src="@/assets/svg/menu/quiz.svg"><span class="title-over">간단퀴즈</span>
                    </li>
                    <li @click="loadComponent('poll')" v-if="IsPoll"><img src="@/assets/svg/menu/poll.svg"
                                                                          style="width:20px;"><span
                            class="title-over">설문조사</span> <span class="is-dot" v-if="IsPoll"></span></li>
                    <li @click="loadComponent('vote')" v-if="IsVote"><img src="@/assets/svg/menu/vote.svg"
                                                                          style="width:35px;padding-top:2px;"><span
                            class="title-over">투표</span> <span class="is-dot" v-if="IsVote"></span></li>
                    <li @click="loadComponent('data')"><img src="@/assets/svg/menu/data.svg"
                                                            style="width:35px;padding-top:2px;"><span
                            class="title-over">자료공유</span> <span class="is-dot" v-if="IsData"></span></li>
                    <li @click="loadMobileSubMenu('board')">
                        <img src="@/assets/svg/menu/board.svg" style="width:20px;">
                        <span class="title-over posi-top">{{zoomBoardName}}</span> <span class="is-dot"
                                                                                         v-if="IsZoomBoard"></span>

                    </li>

                </ul>
            </div>
            <v-dialog
                    v-model="subMenuShow"
                    width="250"

                    style="z-index:1000000000000;"
            >
                <div class="sub-menu-box">
                    <div class="sub-menu-header">
                        <div class="sub-menu-title">{{subMenuTitle}}</div>
                        <font-awesome-icon class="icon" icon="fa-solid fa-circle-xmark" @click="subMenuShow=false" />
                    </div>
                    <ul class="sub-box">
                        <li class="sub-item" v-for="item in subMenuList" @click="loadComponent(item.code)">
                            {{item.name}}
                        </li>
                    </ul>
                </div>

            </v-dialog>
        </div>
        <!-------------------- mobile end -------------------->

        <!-------------------- pc start -------------------->
        <div class="header fixed" v-else>
            <div class="logo" v-if="!logoUrl"><img src="@/assets/images/logo.png"></div>
            <div class="logo" v-if="logoUrl"><img :src="logoUrl"></div>
            <span class="subject-box"><span class="live-icon">LIVE</span> &nbsp; {{subject}}</span>
            <div class="menu">
                <ul class="menu-box">
                    <li>
                        <img src="@/assets/svg/menu/exam.svg" style="width:27px;"><span
                            class="title-over posi-top">시험</span> <span class="is-dot" v-if="IsProblem"></span>
                        <ul class="sub-box">
                            <li class="sub-item" @click="loadComponent('exam')">저장된 시험 불러오기</li>
                            <li class="sub-item" @click="loadComponent('makeExam')">즉시 만들기</li>
                        </ul>
                    </li>
                    <li @click="loadComponent('quiz')"><img src="@/assets/svg/menu/quiz.svg"><span class="title-over">간단퀴즈</span>
                    </li>
                    <li @click="loadComponent('poll')" v-if="IsPoll"><img src="@/assets/svg/menu/poll.svg"
                                                                          style="width:20px;"><span
                            class="title-over">설문조사</span> <span class="is-dot" v-if="IsPoll"></span></li>
                    <li @click="loadComponent('vote')" v-if="IsVote"><img src="@/assets/svg/menu/vote.svg"
                                                                          style="width:35px;padding-top:2px;"><span
                            class="title-over">투표</span> <span class="is-dot" v-if="IsVote"></span></li>
                    <li @click="loadComponent('data')"><img src="@/assets/svg/menu/data.svg"
                                                            style="width:35px;padding-top:2px;"><span
                            class="title-over">자료공유</span> <span class="is-dot" v-if="IsData"></span></li>
                    <li>
                        <img src="@/assets/svg/menu/board.svg" style="width:20px;">
                        <span class="title-over posi-top">{{zoomBoardName}}</span> <span class="is-dot"
                                                                                         v-if="IsZoomBoard"></span>
                        <ul class="sub-box">
                            <li class="sub-item" @click="loadComponent('boardWrite')">글 작성</li>
                            <li class="sub-item" @click="loadComponent('board')">게시글 보기</li>
                        </ul>
                    </li>

                </ul>
            </div>
            <div class="right-box">
                <ul class="right-ui">
                    <li class="response-info">
                        <div class="layout-box" id="menu-response-box" @click="openResponse()">
                            <span>{{responseTitle}}</span> {{responseTimeInfo}} <span class="circle-box">
                            <font-awesome-icon class="fa-square" style="font-size: 8px" icon="fa-solid fa-square"/></span>
                        </div>
                    </li>
                    <li class="time-info"><span><font-awesome-icon icon="far fa-clock" class="fa-clock"></font-awesome-icon> 방송시간 </span> {{timeInfo}}</li>
                    <li>
                        <v-btn class="end-button" type="button" @click="endMeeting()">끝내기</v-btn>
                    </li>
                </ul>
            </div>

        </div>
        <!-------------------- pc end -------------------->


        <div class="screenLayout" v-if="screenShow">
            <examView :gno="id" v-if="loadExamShow" @hideEvent="screenBoxHIde"></examView>
            <examMake :gno="id" v-if="loadImmExamShow" @hideEvent="screenBoxHIde"></examMake>

            <pollView :gno="id" v-if="loadPollShow" @hideEvent="screenBoxHIde"></pollView>
            <quizMake :gno="id" v-if="loadQuizShow" @hideEvent="screenBoxHIde"></quizMake>
            <voteView :gno="id" v-if="loadVoteShow" @hideEvent="screenBoxHIde"></voteView>

            <hostDataView :gno="id" v-if="loadDataShow" @hideEvent="screenBoxHIde"></hostDataView>
            <liveFileView :no="fileNo" v-if="loadFileShow" @hideEvent="screenBoxHIde"></liveFileView>

            <div class="board-layout" v-if="zoomBoardUpdateShow || zoomBoardListShow || zoomBoardViewShow">
                <boardRegist :parentId="id" :no="updArticleNo" :compontentType="'live'" v-if="zoomBoardUpdateShow"
                             @hideEvent="zoomBoardBoxHIde"></boardRegist>
                <boardList :parentId="id" :compontentType="'live'" v-if="zoomBoardListShow"
                           @articleView="showArticleView"
                           @hideEvent="zoomBoardBoxHIde"></boardList>
                <boardView :parentId="id" :no="articleNo" :compontentType="'live'" v-if="zoomBoardViewShow"
                           @articleUpdate="updateArticle" @hideEvent="zoomBoardBoxHIde"></boardView>
            </div>
        </div>
        <div class="screenLayout" id="app-response">
            <response :params="userReponseParams" :realUserJoinList="realUserJoinList" :realUserActive="realUserActive"
                      v-if="userReponseShow" @hideEvent="screenBoxHIde"></response>

        </div>
        <div class="blackScreenLayout" v-if="badScreenShow">
            <div class="bad-modal-box">
                <h3>관리자에 의해 라이브 강제 종료</h3>
                <div class="message" v-html="adminBadMessage"></div>
                <div class="button-row">
                    <v-btn class="end-button" type="button" @click="badDoAction()">확인</v-btn>
                </div>
            </div>
        </div>
        <div class="blackScreenLayout" v-if="completeScreenShow">
            <div class="complete-content">
                <div class="title-box">
                    <div class="gtitle">{{subject}} > 라이브 완료</div>
                    <div class="del-box" @click="completeLive()"><font-awesome-icon style="color:#fff;" icon="fas fa-times" /></div>
                </div>
                <div class="contents">
                    <div class="img-box">
                        <h3>방송이 종료되었습니다.</h3>
                        <div class="tip">수고하셨습니다. 사용해 주셔서 감사합니다.</div>
                    </div>
                    <div class="text-box">
                        <div class="subject">{{subject}}</div>
                        <div class="author">{{userName}}</div>
                        <ul class="result-ul">
                            <li>
                                <div class="li-title">참가자 수</div>
                                <div class="li-value">{{joinCnt}}</div>
                            </li>
                            <li>
                                <div class="li-title">최대 시청자 수</div>
                                <div class="li-value">{{watchCnt}}</div>
                            </li>
                            <li>
                                <div class="li-title">라이브 시간</div>
                                <div class="li-value">{{liveTime}}</div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="button-row">
                    <v-btn class="cancle" type="button" @click="completeLive()">닫기</v-btn>
                    <v-btn class="end-button" type="button" @click="goAfter()">사용후 관리 살펴보기</v-btn>
                </div>
            </div>
        </div>

    </div>

</template>


<script>
    import examView from '../../../video/setting/exam/examView'
    import examMake from '../../../video/setting/exam/examMake'
    import pollView from '../../../video/setting/poll/pollView'
    import quizMake from '../../../video/setting/quiz/quizMake'
    import voteView from '../../../video/setting/vote/voteView'
    import boardList from '../../../video/setting/board/boardList'
    import boardRegist from '../../../video/setting/board/boardRegist'
    import boardView from '../../../video/setting/board/boardview'
    import hostDataView from '../../../video/setting/data/hostDataView'
    import liveFileView from '../../../video/setting/data/liveFileView'
    import response from '../../../video/setting/userResponse/response'

    import AUTH from '@/config/web/auth'
    import JQ from 'jquery'
    import apiService from "@/config/web/apiService";
    import {
        API_URL_BROADCAST_LIVE_END_USER_INFO,
        API_URL_BROADCAST_GET_LOGO
    } from "@/config/web/urls";

    import hostLiveHandler from '../../handler/hostLiveHandler';
    import hostLiveComuHandler from '../../handler/hostLiveComuHandler';


    export default {
        name: 'hostVideoComponent',
        props: ["meetingInfo","meetingStart"],
        components: {
            examView,
            examMake,
            pollView,
            quizMake,
            voteView,
            boardList,
            boardRegist,
            boardView,
            response,
            hostDataView,
            liveFileView

        },
        watch: {
            meetingStart(val) {
                if(val && !this.meetingInfo.info.start_time) {
                    this.setLiveTime();
                }
            }
        },
        data() {
            return {
                DEBUG: ((process.env.NODE_ENV == 'development') ? true : false),
                mobile: this.$isMobile(),
                mobileClass: ((this.$isMobile()) ? 'mobile' : ''),
                insertFlag: false,
                id: '',
                subject: '',
                uid: '',
                userName: '',
                userEmail: '',
                topMenuShow: false,
                screenShow: false,
                loadExamShow: false,
                loadDataShow: false,
                headerClass: '',
                loadImmExamShow: false,
                loadPollShow: false,
                loadVoteShow: false,
                loadQuizShow: false,
                userReponseShow: false,
                userReponseParams: {},
                brdType: '',
                isAttendShow: false,
                waitUserList: [],
                selectedUserIdx: '',
                noticeShow: false,
                sendParentParams: {},
                webinaDataShow: false,
                adminBadMessage: '',
                badScreenShow: false,
                zoomBoardName: '게시판',
                zoomBoardUpdateShow: false,
                zoomBoardListShow: false,
                zoomBoardViewShow: false,
                articleNo: '',
                timeInterval: '',
                timeInfo: '00:00:00',
                updArticleNo: '',
                IsProblem: false,
                IsData: false,
                IsPoll: false,
                IsVote: false,
                IsZoomBoard: false,
                responseTitle: '',
                responseTimeInfo: '',
                timeResponseInterval: null,
                logoUrl: '',
                completeScreenShow: false,
                joinCnt: '',
                watchCnt: '',
                liveTime: '',
                goAfterFlag: false,
                fileNo: '',
                loadFileShow: false,
                memberInfo: {},
                streamRoomId: '',
                userJoinShow: false,
                waitUserShow: false,
                dbWaitUserList: [],
                realUserJoinList: {},
                realUserActive: false,
                hostEndActive: false,
                subMenuShow: false,
                subMenuList: [],
                subMenuTitle: '',

            }
        },
        created() {

            AUTH.setDevice(this.$isMobile());
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.id = this.$route.params.id;
                if (this.id < 1) {
                    this.doError();
                    return;
                }

                this.memberInfo = member.memberInfo;
                this.uid = this.memberInfo.uid;
                this.userName = this.memberInfo.name;
                this.userEmail = this.memberInfo.email;
                this.streamRoomId = this.id + '_' + this.uid;
                this.getMeetingInfo();

            } else {
                this.doError();

            }
        },
        methods: {
            /**** 방송 시작 부분 ****/
            getMeetingInfo() {
                this.id = this.meetingInfo.info.id;
                if (this.meetingInfo.info.uid != this.uid) {
                    this.doError();
                    return;
                }
                if (this.meetingInfo.info.infoStatus == 'complete') {
                    let message = {
                        message: '종료된 방송입니다',
                        doAction: () => {
                            this.doError();
                        }
                    };
                    this.$eventBus.$emit('modalOpen', message);
                    return;
                }
                this.subject = this.meetingInfo.info.subject;
                this.brdType = this.meetingInfo.info.role;
                if(this.meetingInfo.info.start_time) {
                    this.setLiveTime();
                }

                this.logoUrl = (this.meetingInfo.info.logo) ? API_URL_BROADCAST_GET_LOGO + '?logo=' + this.meetingInfo.info.logo : '';
                this.IsProblem = (this.meetingInfo.problemInfo) ? true : false;
                if (this.meetingInfo.fileList) {
                    if (this.meetingInfo.fileList.length > 0) this.IsData = true;
                }
                this.IsPoll = (this.meetingInfo.pollInfo) ? true : false;
                this.IsVote = (this.meetingInfo.voteInfo) ? true : false;
                this.IsZoomBoard = (this.meetingInfo.articleInfo) ? true : false;
                if (this.meetingInfo.boardInfo) this.zoomBoardName = this.meetingInfo.boardInfo.boardName;
                this.$eventBus.$on('rpcJsonTransfer', this.transferAction);


            },
            setLiveTime() {

                let timeCnt = hostLiveHandler.getStartTime();
                this.timeInterval = setInterval(() => {
                    this.timeInfo = hostLiveHandler.setTimeVal(timeCnt);
                    timeCnt++;
                }, 1000);

            },
            /**** 방송 시작 부분 end ****/


            /**
             * @  저장된 정보를 불러오는 부분
             **/
            loadComponent(cmd) {
                if (this.mobile) {
                    this.subMenuShow = false;
                }

                switch (cmd) {
                    case 'exam':
                    case 'quiz':
                    case 'poll':
                    case 'vote':
                        if (this.userReponseShow) {
                            let message = {
                                message: '먼저 진행중인 작업을 종료하세요',
                            };
                            this.$eventBus.$emit('modalOpen', message);
                            return;
                        }
                        this.screenShow = true;
                        switch (cmd) {
                            case 'exam': /// 시험 불러오기
                                this.loadExamShow = true;
                                break;
                            case 'quiz':
                                this.loadQuizShow = true;
                                break;
                            case 'poll':
                                this.loadPollShow = true;
                                break;
                            case 'vote':
                                this.loadVoteShow = true;
                                break;
                        }
                        break;
                    case 'data':
                    case 'makeExam':
                    case 'board':
                    case 'boardWrite':
                        this.screenShow = true;
                        switch (cmd) {
                            case 'data': /// 자료 불러오기
                                this.loadDataShow = true;
                                break;
                            case 'makeExam':
                                this.loadImmExamShow = true;
                                break;
                            case 'board':
                                this.zoomBoardListShow = true;
                                break;
                            case 'boardWrite':
                                this.updArticleNo = 'new';
                                this.zoomBoardUpdateShow = true;
                                break;
                        }
                        break;
                }
            },

            screenBoxHIde(event) {
                const params = hostLiveComuHandler.sendToUser(event, (res) => {
                    const cmd = res.cmd;
                    console.log(res);
                    switch (cmd) {
                        case 'responseClose':
                            this.userReponseShow = false;
                            break;
                        case 'responseOpen':
                            this.realUserJoinList = res.data.map.userList;
                            this.realUserActive = true;
                            setTimeout(() => {
                                this.realUserActive = false;
                            }, 700)
                            break;
                    }


                });
                console.log(params);
                if (params.userReponseParams) {
                    this.userReponseParams = params.userReponseParams;
                }
                if (params.userReponseShow == 'close') {
                    this.userReponseShow = false;
                } else if (params.userReponseShow) {
                    this.userReponseShow = true;
                }
                if (params.open) {
                    this[params.open] = true;
                }
                if (params.fileNo) this.fileNo = params.fileNo;
                else this.fileNo = '';

                if (params.screenShow == 'close') this.screenShow = false;
                if (params.close) this[params.close] = false
                if (params.responseTitle) this.responseTitle = params.responseTitle;
                else this.responseTitle = '';

            },
            /**
             * @  저장된 정보를 불러오는 부분 end
             **/

            /**
             * @ 호스트와 참여자가 정보교환하는 부분
             *
             **/
            transferAction(params) {
                console.log(params)
                switch (params.responseMethod) {
                    case 'makeLiveRoomWaitUsers': // 호스트 처음 입장시 대기실 정보를 가져옴
                        //params.waitUsers
                        this.waitUserList = [];
                        for (const user of this.dbWaitUserList) {
                            if (params.waitUsers[user.uid]) {
                                let wparams = {
                                    uid: user.uid,
                                    name: user.name,
                                    email: user.email,
                                }
                                this.waitUserList.push(wparams);
                            }
                        }
                        if (this.waitUserList.length > 0) this.userJoinShow = true;

                        break;
                    case 'joinRoom':
                        break;
                    case 'adminForceEndMeeting': // 관리자 강제종룟
                        this.adminBadMessage = params.data.replace(/\n/g, "<br />") + '<br>자세한 내용은 관리자에게 문의바랍니다';
                        this.badScreenShow = true;
                        break;
                    case 'replayProblem':
                    case 'replayPoll':
                    case 'replayVote':
                    case 'replayQuiz':
                        let parentParams = {
                            method: 'replyUser',
                            uid: params.data.data
                        };
                        this.$eventBus.$emit('liveUserResponse', parentParams);
                        break;
                    case 'forceOut': // 호스트가 참가자를 퇴장시킴
                        const rpcParams = {
                            sendUid: params.sendUid,
                            sendMethod: 'hostForceOut',
                            responseMethod: 'hostForceOut'
                        }
                        hostLiveComuHandler.rpcSend(rpcParams)
                        break;
                    case 'outUser': // 방송중 종료한 사람들

                        break;
                }
            },
            /**
             * @ 호스트와 참여자가 정보교환하는 부분 end
             *
             **/

            /**
             * @ 게시판
             *
             **/
            zoomBoardBoxHIde(event) {
                //console.log(event)
                switch (event.page) {
                    case 'regist':
                        this.screenShow = false;
                        this.zoomBoardUpdateShow = false;
                        this.zoomBoardListShow = false;
                        this.zoomBoardViewShow = false;
                        break;
                    case 'list':
                        this.screenShow = false;
                        this.zoomBoardUpdateShow = false;
                        this.zoomBoardListShow = false;
                        this.zoomBoardViewShow = false;
                        break;
                    case 'view':
                        this.zoomBoardUpdateShow = false;
                        this.zoomBoardListShow = true;
                        this.zoomBoardViewShow = false;
                        break;
                }

            },
            showArticleView(no) {

                this.articleNo = no;
                this.zoomBoardUpdateShow = false;
                this.zoomBoardListShow = false;
                this.zoomBoardViewShow = true;
            },
            updateArticle(no) {
                this.updArticleNo = no;
                this.zoomBoardUpdateShow = true;
                this.zoomBoardListShow = false;
                this.zoomBoardViewShow = false;
            },

            /**
             * @ 게시판 end
             *
             **/




            doError() {
                JQ(window).off("beforeunload");
                this.$emit('outActive',true)
                if (this.goAfterFlag) {

                    window.opener.document.location.href = '/video/setting/afterExam';
                } else {
                    window.opener.document.location.reload();
                }

            },

            goBannerUrl(url) {
                window.open(url);
            },
            completeLive() {
                this.doError()
            },
            goAfter() {
                this.goAfterFlag = true;
                this.doError()

            },
            endMeeting() {
                let message = {
                    type: 'confirm',
                    message: ' 방송을 종료하시겠습니까?',
                    doAction: () => {
                        setTimeout(() => {
                            let message = {
                                type: 'confirm',
                                message: '정말로 종료하시겠습니까?<br>지금 종료할 경우 라이브가 종료되어 새롭게 라이브를 시작하셔야 합니다',
                                doAction: () => {
                                    this.meetingEnd();
                                }
                            };
                            this.$eventBus.$emit('modalOpen', message);
                        }, 500)
                    }
                };
                this.$eventBus.$emit('modalOpen', message);
            },
            meetingEnd() {

                this.endMeetingActiveAct();
                if (this.timeInterval) {
                    clearInterval(this.timeInterval)
                }

                this.completeScreenShow = true;
                const frm = {parentId: this.id.toString()}
                apiService.post(API_URL_BROADCAST_LIVE_END_USER_INFO, frm, (ret) => {
                    let joinCnt = 0;
                    for (let user of ret.list) {
                        //if (user.status == 'join') {
                            joinCnt++;
                        //}
                    }
                    this.joinCnt = joinCnt;
                    this.watchCnt = ret.list.length;
                    this.liveTime = this.timeInfo;
                })

            },
            endMeetingActiveAct() {
                hostLiveHandler.doEndMeetingActive(this.$isMobile())


            },
            badDoAction() {
                this.badScreenShow = false;
                hostLiveHandler.doEndMeetingActive(this.$isMobile())
                this.$emit('outActive',true)


            },
            openResponse() {
                JQ('#app-response').show();
                JQ('#menu-response-box').hide();

            },
            updateTopMenu() {
                this.topMenuShow = true;
                this.headerClass = 'fixed';
            },

            /**
             * @  모바일 추가
             *
             **/
            showMenu() {
                let left = 0;
                if (JQ('#left-menu-box').css('left') == '0px') {
                    left = '-100%'
                    JQ('#left-menu-box .bg-screen').hide();
                } else {
                    JQ('#left-menu-box .bg-screen').show();
                }
                JQ('#left-menu-box').animate({
                    left: left,
                }, 200)

            },
            loadMobileSubMenu(cmd) {
                let examMenus = [
                    {name: '저장된 시험 불러오기', code: 'exam'},
                    {name: '즉시 만들기', code: 'makeExam'},
                ]
                let boardMenus = [
                    {name: '글 작성', code: 'boardWrite'},
                    {name: '게시글 보기', code: 'board'},
                ]
                switch (cmd) {
                    case 'exam':
                        this.subMenuTitle = '시험';
                        this.subMenuList = examMenus;
                        break;

                    case 'board':
                        this.subMenuTitle = this.zoomBoardName;
                        this.subMenuList = boardMenus;
                        break;


                }
                this.subMenuShow = true;
            },
        },
        mounted() {
            JQ(window).on("beforeunload", (eventObject) => {
                if(this.$isMobile()) {
                    hostLiveHandler.mobileRecodeChatAct()
                } else {
                    hostLiveHandler.recodeChatAct();
                }
                hostLiveHandler.updateLiveTime();
                return false;
            });
        },
        destroyed() {
            this.$eventBus.$off('rpcJsonTransfer');
            if (this.timeInterval) {
                clearInterval(this.timeInterval)
            }
            if (this.timeResponseInterval) {
                clearInterval(this.timeResponseInterval)
            }
        }

    }
</script>
